.load {
    background: linear-gradient(118.38deg, #29323c -4.6%, #485563 200.44%);
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    position: absolute;
    z-index: 1;

    &__container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 100vh;
    
        @media(min-width: 640px) {
        box-shadow: -8px -8px 20px #2a333d;
    
            width: 650px;
            margin: 0 auto;
        }
    }

    &__title {
        color: white;
        font-family: "Roboto", sans-serif;
        opacity: 0.1;
        font-weight: 700;
        margin: 0;

        &-horow {
          font-size: 40px;
          margin: initial;
        }

        &-gg {
            font-size: 15px;
            letter-spacing: 3px;
            margin: initial;
            text-align: center;
        }
    }
}

.footer {
    box-shadow: -8px -8px 20px #2a333d;
    height: 65px;
    display: flex;
    align-items: center;
    position: fixed;
    bottom: 0;
    width: 100%;

    @media(min-width: 640px) {
      width: 650px;
    }

    &__text {
      font-family: "Roboto", sans-serif;
      font-weight: 600;

      color: rgba(0, 0, 0, 0.214);
    }

    &__container {
        display: flex;
        flex-direction: row;
        align-items: center;
        margin: 0 auto;
        gap: 10px;
    }

    &__img {
        width: 40px;
        height: 40px;
    }
}

.get {
    visibility: hidden;
}