html {
    margin: 0;
}

.main {
    // background-color: #fff;
    height: 100vh;
    width: 100%;
    // background-image: url('https://pibig.info/uploads/posts/2022-08/1659362752_5-pibig-info-p-temno-fioletovii-fon-odnotonnii-krasivo-6.jpg');
//   background: linear-gradient(90deg, #29323c 0%, #485563 100%);

background: linear-gradient(118.38deg, #29323c -4.6%, #485563 200.44%);
// background: linear-gradient(118.38deg, #29323c -4.6%, #485563 200.44%);

    // box-shadow: -8px -8px 20px #2a333d, 10px 7px 20px #475462;

margin: 0;

    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    font-family: "Roboto", sans-serif;
    font-weight: 400;

    position: absolute;

    &__title {
        color: white;
        text-align: center;
        margin: 0;
        padding: 10px 0;
    }

    &__container {
        width: 80%;
        margin: 0 auto;
        // box-shadow: -8px -8px 20px #2a333d;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 100vh;
    
        @media(min-width: 640px) {
        box-shadow: -8px -8px 20px #2a333d;

            width: 650px;
            margin: 0 auto;
        }
    }

    &__input {
        border: 0px;
        width: 100%;
        height: 35px;
        // line-height: 60px;
        background-color: rgba(255, 255, 255, 0.476);
        outline: none;
        color: white;
        font-family: "Roboto", sans-serif;
        // display: flex;
        // align-items: center;
        // justify-content: center;
        // line-height: 35px;
        // margin: 0 auto;
        padding: 0 5px;
        border-radius: 10px;

        @media (min-width: 640px) {
            width: 300px;
        }


    }

    &__buttons {
        text-align: center;
        width: 100%;
    }

    &__button {
        width: 100%;
        height: 50px;
        border-radius: 10px;
        margin-top: 10px;

        background-color: #2F4974;
        border: 0px;
        
        color: white;
        font-size: 15px;
        font-family: "Roboto", sans-serif;
        font-weight: 400;

        @media (min-width: 640px) {
            width: 300px;
        }
    }

    &__players {
        box-sizing: border-box;
        list-style: none;
        color: white;
        background-color: rgba(255, 255, 255, 0.358);
        width: 100%;
        padding: 10px;
        margin: 0 auto;
        margin-top: 10px;

        border-radius: 10px;

        @media (min-width: 640px) {
            width: 300px;
        }
    }

    &__player {
        font-family: "Roboto", sans-serif;
        font-weight: 400;

        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;

        &-cross {
            width: 10px;
            height: 10px;
        }
    }

    &__img {
        &-arrow {
            width: 30px;
            height: 30px;

            transform: rotate(-90deg);
        }
    }
}

.slide {
    transform: translateX(100%);
}

.hidden {
    // display: none;
    visibility: hidden;
}