@import '../App/App.scss';

%fonts {
    font-family: 'Roboto';
    font-weight: 400;
  }
  
.game {
    margin: 10px;
    height: 100vh;
    width: 100%;
    margin: 0;
    color: white;
    background: linear-gradient(118.38deg, #29323c -4.6%, #485563 200.44%);
// background: linear-gradient(118.38deg, #29323c -4.6%, #485563 200.44%);

    // box-shadow: -8px -8px 20px #2a333d, 10px 7px 20px #475462;
    box-shadow: -8px -8px 20px #2a333d;

    &__wrapper {
      // width: 100vh;
      // height: 100vh;
      // background: linear-gradient(118.38deg, #29323c -4.6%, #485563 200.44%);
    }


    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    @media(min-width: 640px) {
        width: 650px;
        margin: 0 auto;
    }

    z-index: 1;

    @extend %fonts;
    &__input {
    @extend %fonts;

        border: 0px;
        margin-bottom: 5px;
        padding: 10px;
        width: 200px;
        border-radius: 10px;
    }

    &__container {
        display: flex;
        flex-direction: column;
        gap: 50px;

        width: 300px;
    }

    &__field {
    @extend %fonts;

      width: 100%;
      height: 40px;
    
      &-player {
        margin: 0 auto;
        margin-bottom: 10px;
        text-align: center;
        font-size: 30px;

        &--bold {
            color: yellow
        }
      }

      &-current {
        box-sizing: border-box;
        background-color: rgba(255, 255, 255, 0.366);
        height: 45px;
        height: max-content;
        min-height: 45px;
        border-radius: 10px;
        padding: 10px;

        display: flex;
        justify-content: center;
        align-items: center;

        @media (min-width: 640px) {
            width: 300px;
            margin: 0 auto; 
        }
      }
    }

    &__buttons {
        box-sizing: border-box;
        width: min-content;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: auto;
        background-color: rgba(255, 255, 255, 0.308);
        padding: 10px;
        border-radius: 10px;

        font-size: 18px;
        font-weight: 500;

        width: 100%;
    }

    &__button {
    @extend %fonts;

      width: 100%;
      height: 60px;
      border: 0px;
      border-radius: 10px;
      color: #fff;
      font-size: 18px;
      font-weight: 500;

      &-start {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: auto;
        background-color: #2F4974;
        margin-top: 10px;
      }

      &-true {
        background-color: #84DC4E;

        &:active {
          background-color: #7BCD48;
        }
      }

      &-dare {
        background-color: #F17474;

        &:active {
            background-color: #F35353;
        }
      }

      &-disabled {
        background-color: #85dc4e28;
        color: rgba(255, 255, 255, 0.336);
        
        &:active {
          background-color: #85dc4e28;
        }
      }

      &-img {
        width: 30px;
        height: 30px;
        color: wheat;
      }

      &-restart {
        border-radius: 50%;
        width: 60px;
      }

      &-back {
        background-color: #F35353;
        width: 100px;
        margin-top: 10px;

        &:active {
            transform: scale(0.9);
        }
      }
    }
}